import React, { FC, useState } from "react";
import Dialog, {
  DialogContent,
  DialogFooter,
  DialogButton,
} from "@material/react-dialog";

const DialogCheck: FC = () => {
  const [isOpen, setOpen] = useState(true);

  return (
    <Dialog onClose={(action: string) => setOpen(false)} open={isOpen}>
      <DialogContent>
        <p>Discard Draft?</p>
      </DialogContent>
      <DialogFooter>
        <DialogButton action="dismiss">Cancel</DialogButton>
        <DialogButton action="discard" isDefault>
          Discard
        </DialogButton>
      </DialogFooter>
    </Dialog>
  );
};

export default DialogCheck;
